import React, { Component } from "react";
import "./Footer.css";
import Logo from "../Logo/Logo.js";

class Footer extends Component {
  constructor(props) {
    super();
  }

  getHebrewFooter() {
    return (
      <div className="footerDiv">
        <div className="pageFooterHebrew">
          <hr className="breakLine" />
          <div className="footerLogoHebrew">
            <Logo />
          </div>
          <div className="verticalLineHebrew" />
          <h3 className="ContactUsTitleHebrew">צור קשר</h3>
          <div className="footerObjectHebrew">
            {"(+972)9-8942635"}
            <br />
            <a href="mailto:kerentzvika@gmail.com" className="footerMailLink">
              kerentzvika@gmail.com
            </a>
            <br />
            {" שדרות לורנס 25, לורנס, ניו יור"}
          </div>
        </div>
        <br />
      </div>
    );
  }

  getEnglishFooter() {
    return (
      <div className="footerDiv">
        <div className="pageFooter">
          <hr className="breakLine" />
          <div className="footerLogo">
            <Logo />
          </div>
          <div className="verticalLine" />
          <h3 className="ContactUsTitle">contact us:</h3>
          <div className="footerObject">
            {"(+972)9-8942635"}
            <br />
            <a href="mailto:kerentzvika@gmail.com" className="footerMailLink">
              kerentzvika@gmail.com
            </a>
            <br />
            {"25 Lawrence Avenue, Lawrence, NY"}
          </div>
        </div>
        <br />
      </div>
    );
  }

  getFooter() {
    if (this.props.getLanguage() === "Hebrew") return this.getHebrewFooter();
    if (this.props.getLanguage() === "English") return this.getEnglishFooter();
    return null;
  }

  render() {
    return this.getFooter();
  }
}

export default Footer;
